import React from 'react';
import AnnounsmentItem from './AnnounsmentItem';


export const AnnounsmentList = ({ announcements }) => {

        return (
            <div className="Add">
                <ul>
                    {announcements.map(announcement =>
                        (<AnnounsmentItem announcement={announcement}/>)
                    )
                    }
                </ul>

            </div>
        );

}
